<template>
    <div class = "login">
        <div class = "login_title">{{loginorSignup}}</div>
        <form :action="userInfo.loginFlag ? 'login': 'signup' " enctype="application/x-www-form-urlencoded" method="POST">
            <div class = "login_input">
                <input type="text" v-model="userInfo.username" name = "username" placeholder="请输入用户名">
            </div>
            
            <div class = "login_input">
                <input type="password" v-model="userInfo.password" name = "password" placeholder = "请输入密码">
            </div>

            <div v-show = "!userInfo.loginFlag" class = "login_input">
                <input type="password" v-model="userInfo.rePassword" placeholder="再次输入密码">
            </div>

            <div v-show = "!userInfo.loginFlag" class = "login_input">
                <input type="text" v-model="userInfo.email" name="email" placeholder="请输入邮箱">
            </div>

            <div class = "submit">
                <button type="submit" @click = "checkSubmit" class="submit_input hvr-shutter-out-horizontal">{{loginorSignup}}</button>
            </div>

        </form>
        <div v-show= "errorMsg != ''" class = "wrong" >{{errorMsg}}</div>
    </div>
</template>

<script>
export default {
    data() 
    {
        return {
            userInfo : 
            {
                username : "",
                password : "",
                rePassword : "",
                email : "",
                loginFlag : 1
            },
            errorMsg : "",
        }
    },
    computed : 
    {
        loginorSignup()
        {
            // console.log(this);
            return this.userInfo.loginFlag ? "登录" : "注册";
        },
    },
    methods : 
    {
        changeLogin(value)
        {
            this.userInfo.loginFlag = value;
            this.errorMsg = "";
        },
        checkSubmit(e)
        {
            if(this.userInfo.password === "" || this.userInfo.username === "" )
            {
                this.errorMsg = "输入不符合要求！";
                e.preventDefault();
            }
            else if (!this.userInfo.loginFlag && (this.userInfo.emai === "" || this.userInfo.password !== this.userInfo.rePassword))
            {
                this.errorMsg = "输入不符合要求！";
                e.preventDefault();
            }
        }
    },
    mounted()
    {
        this.$bus.$on("changeLogin", this.changeLogin);
    },
    BeforeDestroy()
    {
        this.$bus.$off("changeLogin");
    }
}
</script>

<style scoped>
.login {
    margin: 50px auto auto auto;
    width: 500px;
    height: 600px;
    border: 1px solid #bfbfbf;
    transition: all 0.5s ease 0s;
    background-color: #fff2f9;
}

.login:hover {
    box-shadow: 0 0 3px 0px;
}

.login_title {
    font-size: 20px;
    padding-bottom: 20px;
    padding-top: 20px;
    text-align: center;
    color: #212121;
    border-bottom: 1px solid #bfbfbf;
}

.login_title:hover {
    color: #ff409f;
}

.login_input {
    margin: 45px auto;
    width: 400px;
    height: 40px;
}


.login_input input {
    width: 400px;
    height: 40px;
    outline: none;
    border: 1px solid #bfbfbf;
    padding-left: 7px;
    border-radius: 5px;
}

.login_input input:focus {
    border-color: #ff7fbf;
}

.submit {
    margin: auto;
    width: 400px;
    height: 40px;
    padding-top: 24px;
}

.submit_input {
    margin: 0px 125px;
    width: 150px;
    height: 50px;
    /* background-color: rgb(255, 0, 0); */
    font-weight: 550;
    font-size: 18px;
    border-style: none;
    /* 鼠标变手 */
    cursor: pointer
}

/* Shutter Out Horizontal */
.hvr-shutter-out-horizontal {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    background: #d4d4d4;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.hvr-shutter-out-horizontal:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #ff7fbf;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 50%;
    transform-origin: 50%;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.hvr-shutter-out-horizontal:hover,
.hvr-shutter-out-horizontal:focus,
.hvr-shutter-out-horizontal:active {
    color: white;
}

.hvr-shutter-out-horizontal:hover:before,
.hvr-shutter-out-horizontal:focus:before,
.hvr-shutter-out-horizontal:active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

.wrong {
    margin: 45px auto;
    width: 200px;
    height: 40px;
    text-align: center;
    font-size: 22px;
    color: red;
}
</style>